a {text-decoration:none; color:#555;}
body {font-family:orbitron;}

#wrap {position:fixed; top:0; left:0; width:100%; height:100%;
				min-width:1400px; min-height:780px; 	background-image:url(../public/img/bg_morning.jpg);
				background-size:cover; transition:all 2s;}
#wrap h1 {width:90px; height:80px; position:fixed; top:30px; left:60px; 
						background-image:url(../public/img/logo_b.png); background-size:contain; 
						background-repeat: no-repeat;}				
#gnb { position:fixed; top:50px; right:200px;}
#gnb li { float:left; margin-left:30px;}
#gnb li a { font-size:14px; font-weight:bold; color:#555;}

article { position:absolute; top:350px; left:150px; width:450px; }
article p { font-size:50px; color:#666;}
article p strong { color:#111;}
article em { font-size:14px; font-style:normal; color:#333;}

figure { position:absolute; top:200px; right:-300px; width:1226px; 
height:809px; background-image:url(../public/img/phone_morning.png);
 transition:all 2s;}
 figure p { position:absolute; top:100px; left:137px; width:540px; 
height:307px; overflow:hidden; font-size:60px; font-weight:bold; color:#333; line-height:4.6; box-sizing:border-box; padding-left:50px;}
 
nav { position:fixed; bottom:70px; left:6%; width:88%; }
nav li { float:left; width:25%;}
nav li a { display:block; width:98%; height:35px; background:#fff;
 color:#111; margin:0 auto; transition:all 0.5s; font-size:13px;
 font-weight:bold; line-height:3; letter-spacing:3px; box-sizing:border-box;padding-left:30px; opacity:0.6; z-index:1;}
/* nav li:hover a { background:#111; color:#ddd; opacity:0.9; } */
nav ul li.on a {background:#131313; color:#fff;}

 /*배경 구름*/
#wrap>img:nth-child(1) { position:absolute; top:0px; left:-1146px; 
animation:ani1 110s linear  infinite; opacity:0.8;}
#wrap>img:nth-child(2) { position:absolute; top:20px; left:0px; 
animation:ani2 100s linear  infinite; opacity:0.8;}

/*모바일 구름*/
figure p img.cl1 { width:600px; height:auto; position:absolute; top:30px;
 left:-600px; animation:ani3 110s linear infinite; z-index:4;}
figure p img.cl2 { width:700px; height:auto; position:absolute; top:20px; 
left:0px; animation:ani4 100s linear infinite; z-index:4;}

/*구름 모션*/
@keyframes ani1 {
	0%{ left:-1146px;}
	100% { left:1950px;}
}

@keyframes ani2 {
	0%{ left:0px;}
	50% { left:1950px; opacity:0;}
	50.001% { left:-1840px; opacity:0.8;}
	100% { left:0px;}
}

@keyframes ani3 {
	0%{ left:-600px;}
	100% { left:540px;}
}
@keyframes ani4 {
	0%{ left:0px;}
	50% { left:540px; opacity:0;}
	50.001% { left:-700px; opacity:1;}
	100% { left:0px;}
}	

 /*아침 테마*/
#wrap.morning { background-image:url(../public/img/bg_morning.jpg);}
#wrap.morning h1 { background-image:url(../public/img/logo_b.png);}
#wrap.morning #gnb a { color:#555;}
#wrap.morning figure { background-image:url(../public/img/phone_morning.png);}
#wrap.morning article p { color:#666;}
#wrap.morning article p strong { color:#111;}
#wrap.morning article em { color:#333;}
#wrap.morning figure p { color:#333;}

 /*오후 테마*/
#wrap.afternoon { background-image:url(../public/img/bg_afternoon.jpg);}
#wrap.afternoon h1 { background-image:url(../public/img/logo_b.png);}
#wrap.afternoon #gnb a { color:#555;}
#wrap.afternoon figure { background-image:url(../public/img/phone_afternoon.png);}
#wrap.afternoon article p { color:#666;}
#wrap.afternoon article p strong { color:#111;}
#wrap.afternoon article em { color:#333;}
#wrap.afternoon figure p { color:#333;}

 /*저녁 테마*/
#wrap.evening { background-image:url(../public/img/bg_evening.jpg);}
#wrap.evening h1 { background-image:url(../public/img/logo_w.png);}
#wrap.evening #gnb a { color:#fff;}
#wrap.evening figure { background-image:url(../public/img/phone_evening.png);}
